'use client';
import FileCard from "@/components/FileCard/FileCard";
import useGetFiles from "@/hooks/useGetFiles";
import DownloadManager from "@/components/DownloadManager/DownloadManager";

export default function Home() {
  const {files, loading} = useGetFiles();
  
  return (
    <main className="bg-gray-900 w-dvw h-dvh flex flex-row flex-wrap gap-5 p-10 content-start justify-center overflow-auto"> 
      {
        loading ? (
          <h1 className="font-display text-sky-50">Loading...</h1>
        ) : (
          files.map((file) => (
            <FileCard file={file} key={file._id} />
          ))
        )
      }
      <DownloadManager />
    </main>
  );
}
